@font-face {
  font-family: Avenir-Book;
  src: url("Avenir-Book.657c3568.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: Avenir-Heavy;
  src: url("Avenir-Heavy.01d9ab74.ttf") format("truetype");
  font-weight: 800;
  font-style: normal;
}

:root {
  --border: 1px solid #e1e7ed;
  --box-shadow-input: 0 1px 8px 0 #3d4e611a;
  --box-shadow-input-efect: 0 3px 8px 0 #3d4e6133;
}

body {
  margin: 0;
}

input {
  border: none;
  font-family: Avenir-Book, sans-serif;
  font-weight: 300;
}

.form {
  padding: 0 10px;
}

.form__form-big {
  margin: 20px 0;
}

.form__form-big, .form__form-sm {
  position: relative;
}

.form__form-big__input, .form__form-sm__input {
  border: var(--border);
  box-shadow: var(--box-shadow-input);
  border-radius: 4px;
  width: 100%;
}

.form__form-big__input {
  padding: 23px 60px;
  font-size: 16px;
}

.form__form-sm__input {
  padding: 11px 31px;
  font-size: 14px;
}

.form__form-big__input:focus, .form__form-sm__input:focus {
  border: var(--border);
  box-shadow: var(--box-shadow-input-efect);
  outline: none;
  font-family: Avenir-Heavy, sans-serif;
}

.form__form-big__input:hover, .form__form-sm__input:hover {
  box-shadow: var(--box-shadow-input-efect);
}

.form__form-big__input::placeholder, .form__form-sm__input::placeholder {
  color: #3d4e61;
}

.form__form-big--icon:before {
  content: "";
  background: url("Search.ccd3c07b.svg") center / cover;
  width: 19px;
  height: 19px;
  position: absolute;
  top: 50%;
  left: 25px;
  transform: translateY(-50%);
}

.form__form-sm--icon:before {
  content: "";
  background: url("Search.ccd3c07b.svg") center / cover;
  width: 11px;
  height: 11px;
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

